@import url('https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');



a:hover{
    text-decoration: none !important;
}
body{
    background-image: linear-gradient(135deg,#0061ff,#60efff,#0061ff) ;
    background-size: 400% 400%;
    background-attachment: fixed;
    font-family: Nunito;
    animation: gradient 15s ease-in-out infinite;
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


::-webkit-scrollbar{
    width: 10px;
}
::-webkit-scrollbar-thumb{
    background-image: linear-gradient(#0061ff,#60efff) ;
}
.App > .container{
    position: absolute;
    top: 40px; left: 0; bottom: 0; right: 0;
}
.App > .container >.row{
    height: 90vh;
}

.main_area{
    background-color: antiquewhite;
    height: 91vh;
    overflow-x: hidden;
    overflow-y:auto ;
    border-radius: 15px;
    padding: 25px 15px;
    box-shadow: 0 1px 7px #000;
}
/* sidebar

/*  */
.sidebar {
    background-color: #fff;
    height: 100%;
    border-radius: 20px;
    padding: 25px 15px;
    box-shadow: 0 0 10px black;
    text-align: center;
}
.sidebar a {
    width: 100%;
    text-decoration: none;
    color: #354548;
    transition: font-size .3s linear;
}
.sidebar a:hover {
    text-decoration: none;
    font-size: 1.1rem;
    color: #000;
}
.sidebar_email a:hover {
    width: 100%;
    text-decoration: none;
    font-size: 1rem;
    color: #000;
    transition: font-size .3s linear;
}
.sidebar_image {
    height: 110px;
    width: 120px;
    object-fit: contain;
    border-radius: 30px;
}

.sidebar_icon {
    width: 25px;
    height: 25px;
}

.sidebar_name {
    font-family: 'Kaushan Script';
    margin: 20px 0;
    font-size: 1.7rem;
}

.sidebar_name>span {
    color: #00F260;
}

.sidebar_item {
    margin-bottom: 6px;
    padding-top: 6px;
    height: 40px;
    font-size: 1.05rem;
    border-radius: 30px;
}

.sidebar_title {
    background-color: #f1f1f1;
    border-radius: 30px;
}

.sidebar_contact {
    background-color: #dbdddf;
    padding: 2px 0;
    margin: 20px -15px;

}

.sidebar_email {
    background-image: linear-gradient(to right bottom, #00F260, #0575E6);

    border-radius: 30px;
    color: #fff;
    margin: 15px 25px 0px;
    
    transition: all .3s linear;
}

.sidebar_email:hover {
    transform: scale(1.1);
}

.sidebar_github:hover,
.sidebar_resume:hover {
    background-color: #f1f1f2;
    color: #000;
}

/* Navbar */
.navbar .items{
    display: flex;
}

.navbar .active{
    font-size: 2rem;
    font-weight: 700;
    color: #354358;
    border-bottom: 4px solid #00f260;
}

.navbar .navitem{
    margin-right: 20px;
    text-decoration: none;
    color: #352358;
    transition: transform .3s linear;
    font-size: 1.3rem;
}

.navbar .navitem:hover{
    text-decoration: none;
    transform: scale(1.15);
    background: -webkit-linear-gradient(#0061ff,#60efff);
    background-clip: text;
    -webkit-backdrop-clip : text;
    -webkit-text-fill-color: transparent;
}
/* About */

.about {
    height: 72vh;
    padding: 20px;
    margin:-35px;
}
.about .intro {
    font-size: 1.1rem;
    padding: 1px;
    margin: 30px 35px;
}
.about .container{
    background-color: #dbdddf;
    padding: 20px;
    overflow: auto;
    max-height: 58vh;

}
.about .heading{
    font-size: 25px;
    font-weight: 700;
    color: #353535;
}
/* SkillCard */
.skill_card{
     background-color: #fff;
     margin: 7px 10px;
     padding: 0 10px;
     display: flex;
     flex-direction: row;
     border-radius: 10px;
}
.skill_icon{
    width: 40px;
}
.skill_body{
    margin:15px;
}
.skill_about{
    margin: 0;
    font-size: 0.9rem;
}
/* Resume */
.resume{
    padding-top: 2px;
}
.resume-card_heading , .resume_language_heading{
    margin-left: 10px;
    font-size: 1.8rem;
    font-weight: 700;
    color: #354538;
}
.resume-card_body{
    margin-top: 10px;
}
.resume-card_title{
    font-weight: 700;
}
.resume-card_details{
    font-size: 0.9rem;
    padding-top:10px ;
    margin: 5px 0;
}


/* Bar */
.bar{
    width: 90%;
    background-color: #f1f2f3;
    border-radius: 20px;
    margin-bottom: 12px;
}

.bar_wrapper{
    
    border-radius:20px ;
    height:34px;
    padding-left: 8px;
    color: #fff;
    font-size: .9rem;
    background-size: 300% 100%;
    transition: all .4s ease-in-out;
}
.bar_wrapper_color{
    background-image:linear-gradient(to right,#0061ff,#60efff,#0061ff,#60efff) ;
}

.bar_wrapper:hover {
    background-position: 100% 0;
    transition: all .4s ease-in-out;
}

.bar_icon{
    color: #fff;
    width: 15px;
    height: 15px;
    margin: 10px;
}
/* Projects */
.projects{
    height: 72vh;
    padding-top:20px ;
    overflow-y: auto;
}
.projectCard_wrapper{
    margin: 10px 0;
    width: 100%;
    height: 100%;
    min-height: 160px;
}

.projectCard_image{
    width: 100%;
    height: 80%;
    max-height: 152px;
    border-radius: 25px;
    border: 3px double transparent;
    background-image: linear-gradient(to right bottom, #00F260, #0575E6);
    transition: transform 0.2s linear;
}
.projectCard_image:hover{
    transform: scale(1.04);
}
.projectCard_icon{
    margin-right: 5px;
    width: 25px;
}
.projectCard_title{
    margin-top: 12px;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.projectCard_title a{
    transition: transform .3s linear;
}
.projectCard_title a:hover{
    transform: scale(1.2);
}
.projects_navbar{
    display: flex;
    margin-bottom: 5px;
}
.projects_navbar > div {
    margin: 0 5px;
    font-size: 1rem;  
    transition: transform 0.3s linear;
}
.projects_navbar > div:hover {
   transform: scale(1.1); 
   background: -webkit-linear-gradient(#0061ff,#60efff);
   background-clip: text;
   -webkit-backdrop-clip : text;
   -webkit-text-fill-color: transparent;
}



@media only screen and (max-width: 992px) {
    .main_area{
        margin: 20px 15px 0 15px;
    }
}